import * as React from 'react';
import { Button, ButtonType } from 'office-ui-fabric-react/lib/button';
import {
  Dialog,
  DialogFooter
} from 'office-ui-fabric-react/lib/dialog';

import { ICanvasDeleteDialogProps } from './IConfirmationDialog';

import Strings from '../../loc/CanvasStrings.resx';

export default function ConfirmationDialog(
  props: ICanvasDeleteDialogProps
): React.ReactElement<ICanvasDeleteDialogProps> {
  const { isOpen, message, onConfirmation, onDismiss, title }: ICanvasDeleteDialogProps = props;

  return (
    <Dialog
      isOpen={isOpen}
      onDismiss={onDismiss}
      title={title}
      subText={message}
    >
      <DialogFooter>
        <Button buttonType={ButtonType.primary} onClick={onConfirmation} ariaLabel={ Strings.ConfirmationDialogYes }>
          { Strings.ConfirmationDialogYes }
        </Button>
        <Button onClick={onDismiss} ariaLabel={ Strings.ConfirmationDialogNo }>
          { Strings.ConfirmationDialogNo }
        </Button>
      </DialogFooter>
    </Dialog>
  );
}

(ConfirmationDialog as React.StatelessComponent<ICanvasDeleteDialogProps>).contextTypes = {
  store: React.PropTypes.object.isRequired
};